<template>
<div>
  <div>
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item><i class="el-icon-menu"></i>商务报价单列表</el-breadcrumb-item>
        <el-breadcrumb-item>添加商务报价</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-container>
      <el-main style="padding-left: 0px;padding-top: 0px">
        <!-- 添加订单基础信息 -->
        <div>
          <el-card style="margin-top:30px;">
            <el-form ref="busfrom" :model="busfrom" :rules="rules" label-width="140px">
                <el-form-item label="所属项目：">
                    <el-select v-model="busfrom.projectId" placeholder="请选择项目">
                        <el-option v-for="item in restaurants" :key="item.value" :label="item.label" :value="item.value"></el-option>
                    </el-select>
                </el-form-item>
                <!-- <el-form-item label="预报价单：">
                    <el-select v-model="busfrom.yuBaoJiaId" placeholder="请选择预报价单">
                        <el-option v-for="item in yubaolist" :key="item.value" :label="item.label" :value="item.value"></el-option>
                    </el-select>
                </el-form-item> -->
                <el-form-item label="产品名称：" prop="productName">
                    <el-input v-model="busfrom.productName" placeholder="请输入产品名称" style="width:20%"></el-input>
                </el-form-item>
                <el-form-item label="车型：" prop="carType">
                    <el-input v-model="busfrom.carType" placeholder="请输入车型" style="width:20%" ></el-input>
                </el-form-item>
                <el-form-item label="客户单位：" prop="clientCompanyName">
                    <el-input v-model="busfrom.clientCompanyName" placeholder="请输入客户单位"  style="width:20%"></el-input>
                </el-form-item>
                <el-form-item label="联系人：" prop="contactPerson">
                    <el-input v-model="busfrom.contactPerson" placeholder="请输入联系人" style="width:20%" ></el-input>
                </el-form-item>
                <el-form-item label="联系电话：" prop="contactPhone">
                    <el-input v-model="busfrom.contactPhone" placeholder="请输入联系电话" style="width:20%" ></el-input>
                </el-form-item>
                <el-form-item label="费用详情：">
                    <!-- <el-upload class="upload-demo"> -->
                    <div style="border: 1px dashed #d9d9d9;width:60%;min-height:128px;">
                        <el-form-item label="纹理加工费：" style="margin-top:15px;" prop="wenLiPrice">
                            <el-input-number  v-model="busfrom.wenLiPrice" controls-position="right" :min="0"></el-input-number>
                        </el-form-item>
                        <el-form-item label="打光及焊接：" style="margin-top:15px;" prop="hanJiePrice">
                            <el-input-number  v-model="busfrom.hanJiePrice" controls-position="right" :min="0"></el-input-number>
                        </el-form-item>
                        <el-form-item label="其他附加费：" style="margin-top:15px;" prop="additionPrice">
                            <el-input-number  v-model="busfrom.additionPrice" controls-position="right" :min="0"></el-input-number>
                        </el-form-item>
                        <el-form-item label="合计：" style="margin-top:15px;" prop="total">
                            <span><el-tag type="warning">  元</el-tag></span>
                        </el-form-item>
                    </div>
                </el-form-item>
                <el-form-item label="付款及交货方式："  style="width:30%"prop="payType">
                    <el-input v-model="busfrom.payType">提模付款</el-input>
                </el-form-item>
                <el-form-item label="预期加工周期："style="width:30%" prop="expectTime">
                    <el-input v-model="busfrom.expectTime"></el-input>
                </el-form-item>
                <el-form-item label="备注：" prop="remark">
                    <el-input type="textarea" style="width:30%"v-model="busfrom.remark"></el-input>
                </el-form-item>
            </el-form>
            <div class="card-bottom" style="float:right;">
              <el-button type="primary" @click="submitForm('busfrom')">提 交</el-button>
              <el-button  @click="resetForm('busfrom')">重 置</el-button>
            </div>
          </el-card>
        </div>
      </el-main>
    </el-container>
  </div>
</div>
</template>
<script>
export default {
  props: {
    title: String
  },
  data() {
    return {
      // upload:'http://39.107.78.95:8080/jlmanager/comm/upload/image',
      // upload: 'http://localhost:8885/config/upload/image',
      table_loading: false,
      restaurants: [],
      yubaolist: [],
      busfrom: {
        tax: '17',
        addUserId: '',
        addUserName: '',
        wenLiPrice: "0",
        hanJiePrice: '0',
        additionPrice: '0',
        expectTime: "商务、技术确认后12天",
        payType: "提模付款"
      },
      rules: {
        productName: [{
            required: true,
            message: '请输入产品名称',
            trigger: 'blur'
          },
          {
            min: 0,
            max: 255,
            message: '长度在 0 到 255 个字符',
            trigger: 'blur'
          }
        ],
        wenlidaima: [{
            required: true,
            message: '请输入纹理代码',
            trigger: 'blur'
          },
          {
            min: 0,
            max: 255,
            message: '长度在 0 到 255 个字符',
            trigger: 'blur'
          }
        ],
        carType: [{
            required: true,
            message: '请输入车型',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 255,
            message: '长度在 1 到 255 个字符',
            trigger: 'blur'
          }
        ],
        clientCompanyName: [{
            required: true,
            message: '请输入客户单位',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 255,
            message: '长度在 1 到 255 个字符',
            trigger: 'blur'
          }
        ],
        contactPerson: [{
            required: true,
            message: '请输入预报价单名称',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 255,
            message: '长度在 1 到 255 个字符',
            trigger: 'blur'
          }
        ],
        contactPhone: [{
            required: true,
            message: '请输入联系人电话',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 255,
            message: '长度在 1 到 255 个字符',
            trigger: 'blur'
          }
        ],
        payType: [{
            required: true,
            message: '请输入付款方式',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 255,
            message: '长度在 1 到 255 个字符',
            trigger: 'blur'
          }
        ],
        expectTime: [{
            required: true,
            message: '请输入加工周期',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 255,
            message: '长度在 1 到 255 个字符',
            trigger: 'blur'
          }
        ],
        remark: [{
          min: 0,
          max: 2048,
          message: '长度在 0 到 2048 个字符',
          trigger: 'blur'
        }]
      },
    }
  },
  methods: {
    submitForm(formName){
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let self = this;
          let parm = self.busfrom;
          parm.addUserId = this.uid;
          parm.addUserName = this.name;
          this.postRequest("/offer/bus/add",parm).then(resp => {
            if (resp && resp.status == 200 && resp.data.code == 0) {
              let id = resp.data.data.id;
              if(id > 0) {
                self.$message.success("添加成功");
                this.$router.push('/offer/bus/list')
              }else {
                self.$message.error("添加基础信息失败");
              }
            } else {
              self.$message.error("添加基础信息失败");
            }
          })
        } else {
          self.$message.error('请检查输入信息的格式');
          return false;
        }
      });
    },
    loadAllProject() {
        let self = this
        this.getRequest("/project/list/added?userId=" + this.uid).then(resp => {
          if (resp && resp.status == 200 && resp.data.code == 0) {
                let item = []
                let re = resp.data.data.addedProject
                console.log(re);
                for (var i = 0; i < re.length; i++) {
                    let elem = {
                        label: '',
                        value: ''
                    }
                    elem.label = re[i].projectName
                    elem.value = re[i].id
                    item.push(elem);
                }
                self.restaurants = item;
            } else {
                console.log("获取列表数据失败");
            }
        });
    },
    // 重置
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  },
  mounted: function() {
    this.username = localStorage.getItem('cp_username');
    this.uid = localStorage.getItem('cp_uid');
    this.name = localStorage.getItem('cp_name');
    this.loadAllProject();
  },
  computed: {
      // totalPrice: function() {
      //     var total = 0;
      //     total = this.busfrom.texturingprice + this.busfrom.polishingprice + this.busfrom.extraprice
      //     this.busfrom.totleprice = total
      //     return total;
      // }
  }
}
</script>



